
import Img from '../../Assets/images/blogPost-img1.PNG'

import './style.css'

 
function BlogPost(props) {
    return (
        
    <div >

        <div className="BlogPostHeadDiv">
        <div className="BlogPostDiv" >
                
                <div className="BlogPostDiv-One">
                   <img src={Img} alt="Broke Img"/>
                   <a href="#"><h5> {props.data.title}</h5></a>
                   
               </div>

                <div className="BlogPostDiv-Two">
                  
                  <p>{props.data.description}</p>
                </div>
        </div>

        <div>

        </div>
        </div>



    </div>
    )
}

export default BlogPost;