

import './style.css'

import supportImg1 from '../../Assets/images/support-1.png'
import supportImg2 from '../../Assets/images/support-2.png'
import supportImg3 from '../../Assets/images/support-3.png'
import supportImg4 from '../../Assets/images/support-4.png'
import supportImg5 from '../../Assets/images/support-5.png'
import supportImg6 from '../../Assets/images/support-6.png'
import supportImg7 from '../../Assets/images/support-7.png'



function SupportOrg()
{
    return (

        <div className="ContainerSupportOrgs">
            <br/>
            <h6><strong>We thank the following organizations for their major commitments to support the Jenkins project.</strong></h6>
            <img className="imgsOrg" src={supportImg1} alt="Support Organizations Image One"/>
            <img className="imgsOrg" src={supportImg2} alt="Support Organizations Image One"/>
            <img className="imgsOrg" src={supportImg3} alt="Support Organizations Image One"/>
            <img className="imgsOrg" src={supportImg4} alt="Support Organizations Image One"/>
            <img className="imgsOrg" src={supportImg5} alt="Support Organizations Image One"/>
            <img className="imgsOrg" src={supportImg6} alt="Support Organizations Image One"/>
            <img className="imgsOrg" src={supportImg7} alt="Support Organizations Image One"/>
            <br/>
            <br/>
            <h6>We thank the following organizations for their support of the Jenkins project through free and/or open source licensing programs.</h6>
            <div>
            <ul className="SupportList">
                <li><a href="#">Atlassian</a> </li>         
                <li><a href="#">Datadog</a></li>
                <li><a href="#">Mac Cloud</a></li>
                <li><a href="#">PagerDuty</a></li>
                <li><a href="#">Sentary</a></li>
                <li><a href="#">XMission</a></li>
                <li><a href="#">Tsinghua University</a></li>
                <li><a href="#">Fastly</a></li>
                <li><a href="#">Sign Up</a></li>
                <li><a href="#">IBM</a></li>
            </ul>
            </div>
            <br/>

        </div>
    );
}

export default SupportOrg;