

import { Navbar } from 'react-bootstrap'
import { Nav } from 'react-bootstrap';
import { NavDropdown } from 'react-bootstrap'
import { Button } from 'react-bootstrap';
import { Form } from 'react-bootstrap';
import { FormControl } from 'react-bootstrap';

const commonHeader = () => {

    return (
        
        <div >
  
         <Navbar bg="dark"   variant="dark" fixed="top">
            <Navbar.Brand href="#home" style={{paddingLeft:20 , paddingRight:20}}><strong>Jenkins</strong></Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mr-auto">

                    <Nav.Link >Blog</Nav.Link>

                    <NavDropdown title="Documentation" id="basic-nav-dropdown" style={{paddingLeft: 5}}>
                        <NavDropdown.Item href="#action/3.1">User Guide</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                    </NavDropdown>


                    <Nav.Link href="#link" style={{paddingLeft: 10}}>Plugins</Nav.Link >

                    <NavDropdown title="Community" id="basic-nav-dropdown" style={{paddingLeft: 5}}>
                        <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                    </NavDropdown>

                    <NavDropdown title="Subprojects" id="basic-nav-dropdown" style={{paddingLeft: 5}}>
                        <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                    </NavDropdown>

                    <NavDropdown title="About" id="basic-nav-dropdown" style={{paddingLeft: 5 , paddingRight:10}}>
                        <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                    </NavDropdown>
                
                    <Button variant="outline-light" >Download</Button>
                </Nav>
                
                <Form inline>
                    <FormControl type="text"  placeholder="Search" className="mr-sm-2" />
                    <Button variant="outline-light" style={{marginLeft:10}}>Search</Button>
                </Form>
            </Navbar.Collapse>
        </Navbar> 
        </div>
        
    )
}


export default commonHeader;