import Slider from '../landingPage/slider';
import SupportOrgs from '../../commonComponents/supports/supportOrg';
import FOOTER from '../../commonComponents/Footer/footer';
import FeatureEvents from "../../commonComponents/FeatureEvents/featureEvents"

import BlogPost from '../../commonComponents/BlogPosts/blogPost'
import {blogPostData} from '../../commonComponents/BlogPosts/blogPostData';



import {Button} from 'react-bootstrap'
import './style.css';
import section_one_img from '../../Assets/images/section-one.PNG'
import Footer from '../../commonComponents/Footer/footer';

function landingPage() 
{
    return (

        <div>

        <div className="section-one">
            <div><img  id="sec_one_img" src={section_one_img} alt="LandingPage section one Image"/></div>
            <div className="inner-section-one">
            <h1><span>Jenkins</span></h1>
            <p>
            <strong>
            Build great things at any scale
            </strong>
            </p>
            
            <p id="inner-section-one-ptag">
                The leading open source automation server, Jenkins provides hundreds of plugins to support building, deploying and automating any project.
            </p>
            <Button variant="dark">Documentation</Button> {"          "}
            <Button variant="danger">Download</Button>{' '}
            
            </div>       
        </div>
        
            < Slider />

            <br/>
            <br/>
            

            <FeatureEvents   />
            <br/>


            {/* Video Tag  */}
            <br/>
            <div>
            <iframe allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" frameborder="0" height="621" src="https://www.youtube.com/embed/_MXtbjwsz3A" width="100%"></iframe>
            
            
            </div>


            <h3>Recent Blog Post</h3>
            {
                
                blogPostData.map((item) =>  <BlogPost  data={item} /> )

            }




        <SupportOrgs />
        <FOOTER />
        </div>
    )
}


export default landingPage;