import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

import './style.css';

import { AccessAlarm, ThreeDRotation } from '@material-ui/icons';
import GetAppSharpIcon from '@material-ui/icons/GetApp';
import TuneSharpIcon from '@material-ui/icons/TuneSharp';
import PowerSharpIcon from '@material-ui/icons/PowerSharp';
import ExtensionSharpIcon from '@material-ui/icons/ExtensionSharp';
import AcUnitSharpIcon from '@material-ui/icons/AcUnitSharp';
import ShuffleSharpIcon from '@material-ui/icons/ShuffleSharp';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

export default function FeatureEvents() {
  const classes = useStyles();

  return (
    <div className={classes.root} className="featureEventContainer">
      <Grid container spacing={1}>
        
        <Grid item xs={4}>
          <Paper className={classes.paper}>
              <div className="featureEventDiv">
                  <div className="featureEventDiv-one" >
                  <  ShuffleSharpIcon color="primary" fontSize="large"/>
                  </div>
                  <div className="featureEventDiv-two">
                  <h5>Continuous Integration and Continuous Delivery</h5>
                  <p>As an extensible automation server, Jenkins can be used as a simple CI server or turned into the continuous delivery hub for any project.</p>
                  </div>
              </div>
          </Paper>
        </Grid>
        <Grid item xs={4}>
          <Paper className={classes.paper}>
          <div className="featureEventDiv">
                  <div className="featureEventDiv-one">
                  <GetAppSharpIcon color="primary"  fontSize="large"/>
                  </div>
                  <div className="featureEventDiv-two">
                  <h5>Easy Installation</h5>
                  <p>Jenkins is a self-contained Java-based program, ready to run out-of-the-box, with packages for Windows, Linux, macOS and other Unix-like operating systems.</p>
                 <br/>
                  </div>
              </div>
          </Paper>
        </Grid>
        <Grid item xs={4}>
          <Paper className={classes.paper}>
          <div className="featureEventDiv">
                  <div className="featureEventDiv-one">
                   <TuneSharpIcon color="primary"   fontSize="large"/>
                  </div>
                  <div className="featureEventDiv-two">
                  <h5>Easy configuration</h5>
                  <p>Jenkins can be easily set up and configured via its web interface, which includes on-the-fly error checks and built-in help.</p>
                  <br/><br/>
                  </div>
              </div>
          </Paper>
        </Grid>
        <Grid item xs={4}>
          <Paper className={classes.paper}>
          <div className="featureEventDiv">
                  <div className="featureEventDiv-one">
                  <PowerSharpIcon color="primary" fontSize="large" />
                  </div>
                  <div className="featureEventDiv-two">
                  <h5>Plugins</h5>
                  <p>With hundreds of plugins in the Update Center, Jenkins integrates with practically every tool in the continuous integration and continuous delivery toolchain.</p>
                  </div>
              </div>
          </Paper>
        </Grid>
        <Grid item xs={4}>
          <Paper className={classes.paper}>
          <div className="featureEventDiv">
                  <div className="featureEventDiv-one">
                  <ExtensionSharpIcon color="primary" fontSize="large" />
                  </div>
                  <div className="featureEventDiv-two">
                  <h5>Extensible</h5>
                  <p>Jenkins can be extended via its plugin architecture, providing nearly infinite possibilities for what Jenkins can do.</p>
                  <br/>
                  </div>
            </div>

          </Paper>
        </Grid>
        <Grid item xs={4}>
          <Paper className={classes.paper}>
          <div className="featureEventDiv">
                  <div className="featureEventDiv-one">
                  < AcUnitSharpIcon color="primary" fontSize="large"  />
                  </div>
                  <div className="featureEventDiv-two">
                  <h5>Distributed</h5>
                  <p>Jenkins can easily distribute work across multiple machines, helping drive builds, tests and deployments across multiple platforms faster.</p>
                  
                  </div>
            </div>
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
}
